.seachhistory{
    width:4rem;
}
.seachhistory .hitory-top{
    width: 4rem;
    height:0.2rem;
}
.seachhistory .hitory-top span:nth-child(1){
    font-weight: 600;
    font-size: 0.14rem;
    line-height: 0.2rem;
    float: left;
}
.seachhistory .hitory-top span:nth-child(2){
    line-height: 0.2rem;
    float: right;
}

.historylist{
    width: 3.8rem;
    height: 3rem;
}
.historylist span{
    line-height: 0.3rem;
    display: inline-block;
    margin-top: 0.2rem;
    background-color: #EBEBEB;
    padding:0.02rem 0.1rem; 
    border-radius: 0.1rem;
    margin-left: 0.1rem;
}
