.address{
    border-bottom: solid .01rem #ccc;
    width: 4.04rem;
    height: .5rem;
    padding: .1rem .05rem;
    position: relative;
}
.address span:nth-of-type(1){
    font-size: .16rem;
    margin: 0 .1rem
}
.address span:nth-of-type(2){
    font-size: .16rem;
}
.address p{
    margin: .05rem .1rem;
    color: #666
}
.add{
    width: 3rem;
    height: .4rem;
    background: #f00;
    text-align: center;
    line-height: .4rem;
    font-size: .14rem;
    color: #fff;
    border-radius: .2rem;
    margin-left: .57rem;
    margin-top: 4.8rem
}
.address .iconfont{
    position: absolute;
    right: .1rem;
    top: .25rem
}