.fenlei{
    padding: 0.1rem;
    height: 1rem;
    display: flex;
    justify-content: space-around;
}
.fenlei figure{
    height: 1rem;
}
.fenlei figure img{
    width: 0.8rem;
    height: 0.6rem;
}
.dress-item{
    padding: 0.1rem;
    width: 1.6rem;
    height: 2.6rem;
    position: relative;
    border-radius: 0.2rem;
    margin-left: 0.2rem;
    box-shadow: 0rem 0rem 0.2rem #bbb;
    float: left;
    margin-top: 0.1rem;
}
.dress-item img{
    width: 1.6rem;
    height: 1.8rem;
    position: absolute;
    top:0.1rem;
    left:0.1rem;
}
.dress-item p{
    width: 1.6rem;
    height: 0.4rem;
    line-height: 0.2rem;
    font-size: 0.14rem;
    font-weight: bold;
    position: absolute;
    top:1.9rem;
    left:0.1rem;
}
.dress-item span{
    display: block;
    color:#f00;
    position: absolute;
    left:0.1rem;
    top:2.5rem;
}
.dress-item button{
    width: 0.6rem;
    height:0.25rem;
    line-height:0.2rem;
    text-align: center;
    border:none;
    background: #f00;
    border-radius: 0.1rem;
    position: absolute;
    top:2.45rem;
    right:0.2rem;
    color:#fff;
    font-size: 0.14rem;
    
}