.souye{
}
.carousel{
    margin-top: 0.05rem;
  }
  .img{
    height:1.5rem;
  }
  .table{
    font-size: 0.14rem;
    margin-top: 0.1rem;
    display: flex;
    justify-content: space-around;
  }
.table figcaption{
    text-align: center;
    font-size: 0.25rem;
    margin-bottom: 0.05rem;
}
.table figure:nth-child(1) figcaption,.table figure:nth-child(3) figcaption,.table figure:nth-child(5) figcaption{
    color:#f00;
}
.table figure:nth-child(2) figcaption{
    color:#0f0;
}
.table figure:nth-child(4) figcaption{
    color:#00f;
}
.tejia{
    padding-top: 0.1rem;
    margin-top: 0.2rem;
    border-radius: 0.1rem;
    box-shadow: 0rem 0rem 0.2rem #bbb;
}
.tejia h2{
    margin-left: 0.2rem;
}
.carousel2{
    margin-top: 0.05rem;
}

.cart{
    width: 3.7rem;
    height: 1.7rem;
}
.cart-item{
    width: 1rem;
    height: 1rem;
    padding: 0.05rem 0.1rem;
    float: left;
}

.cart-item img{
    width: 1rem;
    height: 1rem;
} 
.cart-item p:nth-of-type(1){
    color: #000;
    font-size: 0.14rem;
    padding: 0.05rem 0;
    width:1rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.cart-item p:nth-of-type(2){
    color:#f00;
}

.cart-item p:nth-of-type(3){
    color: #666;
    font-size: 0.12rem;
    padding: 0.05rem 0;
    width:1rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.soeyelist{
    margin-bottom: 0.5rem;
}

.goodscart-zy{
    padding:0.05rem;
    margin-left:0.22rem;
    width: 3.6rem;
    height: 0.9rem;
    position: relative;
    margin-top: 0.1rem;
    border-radius: 0.1rem;
    box-shadow: 0rem 0rem 0.2rem #bbb;
}

.goodscart-zy img{
    width:0.9rem;
    height:0.9rem;
    position: absolute;
    top:0.05rem;
    left:0.05rem;
    
}
.goodscart-zy p:nth-of-type(1){
    width: 2.5rem;
    height:0.4rem;
    font-size: 0.16rem;
    font-weight: bold;
    position: absolute;
    top:0.1rem;
    right:0.1rem;
}
.goodscart-zy p:nth-of-type(2){
    width: 0.5rem;
    height:0.2rem;
    font-size: 0.16rem;
    font-weight: bold;
    position: absolute;
    top:0.65rem;
    left:1.1rem;
    color:#f00;
}
.goodscart-zy .btn{
    width: 0.8rem;
    height: 0.3rem;
    background: #f00;
    color:#fff;
    font-size: 0.16rem;
    line-height: 0.3rem;
    text-align: center;
    position: absolute;
    right:0.1rem;
    top:0.55rem;
    border-radius: 0.15rem;
}

