.banner{
    position: relative;
    margin-top: 0.1rem;
}

.banner span:nth-of-type(1){
    position: absolute;
    top: 0;
    left: 0.05rem;
}
.banner span:nth-of-type(2){
    position: absolute;
    top: 0;
    right: 0.05rem;
}

.info{
    width: 4rem;
    height: 0.2rem;
    padding: 0.05rem;
    margin:0.1rem 0;
}
.info span{
    float: left;
    display: inline-block;
    color:#f00;
    font-size: 0.16rem;
    font-weight: bold;
}
.detaildesc{
    text-align: left;
    color:#000;
    font-size: 0.16rem;
    font-weight: bold;
    margin:0.1rem 0;
}

.yunfei{
    width: 4rem;
    height: 0.2rem;
    padding: 0.05rem;
    margin:0.1rem 0;
    text-align: left;
    font-weight:600;
}

.nowbuy{
    width: 4rem;
    height: 0.3rem;
    padding: 0.05rem;
    text-align: center;
    background:#f00;
    position: fixed;
    left: 0;
    bottom: 0;
}
.nowbuy button{
    border: none;
    background: #f00;
    color:#fff;
    font-weight: bold; 
    font-size: 0.18rem;
    line-height: 0.3rem;
    
}