.center{
    display: flex;
    flex-direction: column;
    width: 4.14rem;
    height: 100%;
    align-items: center;
    position: relative;
}
.header{
    width: 4.14rem;
    height: 2rem;
    background: #e00;
    font-size: .16rem;
    color: #fff;
    position: relative;
}
.header div{
    display: flex;
    margin-top: .4rem;
    margin-left: .1rem
}
.header img{
    width: .6rem;
    height: .6rem;
    border-radius: 50%;
    margin: 0 .1rem
}
.header span{
    position: absolute;
    right: .2rem;
    top: .15rem
}
.goods{
    width: 3.9rem;
    height: 1rem;
    background: #ff0;
    border-radius: .1rem;
    font-size: .16rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    top: 1.5rem;
    background: #fff;
    box-shadow: 0rem .02rem .02rem #ccc
}
.goods div{
    text-align: center;
    width: .97rem;
    height: .6rem;
}
.goods div:nth-child(4){
    border-left:solid #ccc .01rem
}
.goods span{
    font-size: .22rem;
    color: #f00;
}
.goods p{
    margin-top: .1rem
}
.listt{
    width: 3.9rem;
    height: 2.5rem;
    border-radius: .1rem;
    box-shadow: 0rem .01rem .02rem #ccc;
    margin-top: .6rem;
    font-size: .14rem
}
.listt ul{
    width: 3.9rem;
    height: 2.5rem;
}
.listt li{
    width: 3.9rem;
    height: .5rem;
    line-height: .5rem;
    display: flex;
    border-bottom: solid .01rem #eee;
    position: relative;
    margin:0;
    padding:0;
}
.listt li span{
    margin: 0 .1rem
}
.jiantou{
    position: absolute;
    right: .05rem;
}
.listt li span:nth-of-type(2){
    font-size: .12rem
}