.header1{
    width: 3.84rem;
    height: .5rem;
    line-height: .5rem;
    padding: 0 .15rem;
    position: fixed;
    top: 0;
    left: 0;
    border-bottom: solid .01rem #ccc;
    position: relative;
}
.header1 span:nth-child(2){
    font-size: .18rem;
    width: 3.84rem;
    height: .5rem;
    line-height: .5rem;
    text-align: center;
    display: block
}

.header1 span:nth-child(1){
    position: absolute;
    left: .2rem;

}