html, body, ul, li, ol, dl, dd, dt, p, h1, h2, h3, h4, h5, h6, form, fieldset,figure, legend, img { margin:0; padding:0; }
fieldset, figure,img { border:none;margin: 0;padding: 0; }
address, caption, cite, code, dfn, th, var { font-style:normal; font-weight:normal; }
li{ list-style:none; } 
body { color:#333; font:12px/20px Arial,"微软雅黑","SimSun","宋体","Arial Narrow",HELVETICA; background:#fff; overflow-y:scroll; overflow-x:hidden;}
a{text-decoration:none; }


.App {
  text-align: center;
}