.list1{
    width: 4.14rem;
    height: 1.5rem;
    position: relative;
}
.list1 li{
    width: 3.84rem;
    height: .5rem;
    border: solid .01rem #eee;
    display: flex;
    align-items: center;
    padding: 0 .15rem
}
.list1 p{
    width: 1rem
}
.list1 li span:nth-of-type(1){
    margin-left: 2.2rem;
    width: .5rem;
    height: .5rem;
    display: block;
    line-height: .5rem;
    text-align: center
}

.list1 li span:nth-of-type(2){
    font-size: .12rem;
    width: .2rem;
    height: .5rem;
    display: block;
    line-height: .5rem;
    text-align: center
}
.tuichu{
    width: 4.14rem;
    height: .5rem;
    text-align: center;
    line-height: 0.5rem;
    box-shadow: 0rem -.02rem .02rem #eee;
    position: absolute;
    left: 0;
    bottom: 0;
}