
.use{
    width: 4.14rem;
    height: .5rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: .16rem
}
.juan{
    width: 3.84rem;
    height: 0.8rem;
    margin-top: .2rem;
    margin-left: .15rem;
    display: flex
}
.money{
    width: 1rem;
    height: 0.8rem;
    font-size: .4rem;
    line-height: 0.8rem;
    text-align: center;
    background: red;
    color: #fff
}
.money span{
    font-size: .18rem
}
.juan div:nth-child(2){
    width: 2.84rem;
    height: .7rem;
    box-shadow: 0rem 0rem .05rem #eee;
    padding-top: .1rem
}
.jingjuan{
    background: #e00;
    border-radius: .05rem;
    color: #fff;
    margin-left: 0.2rem;
    margin-right: 0.1rem;
    font-size: .14rem
}
.quanpin{
    font-size: .16rem;
    font-weight: 600
}
.juan p{
    margin-top: .1rem;
    margin-left: .2rem
}