.address1{
    width: 3.94rem;
    height: 0.8rem;
    box-shadow: 0 0 .04rem #eee;
    border-radius: .1rem;
    margin: .1rem ;
    border-bottom: .02rem darksalmon dashed;
    padding-top: .2rem
}
.address1 span:nth-of-type(1){
    font-size: .16rem;
    margin: 0 .1rem
}
.address1 span:nth-of-type(2){
    font-size: .16rem;
}
.address1 p{
    margin: .05rem .1rem;
    color: #666;
    font-size: .14rem
}
.store{
    width: 3.64rem;
    height: 2rem;
    box-shadow: 0 0 .04rem #eee;
    border-radius: .1rem;
    margin: 0 .1rem ;
    padding:0 .15rem;
    position: relative;
}
.store>p{
width: 100%;
height: .4rem;
line-height: .4rem;
font-size: .16rem;
}
.store>div{
    display: flex;
    margin-top: .1rem
}
.store>div img{
    width: .8rem;
    height: .8rem;
}


.num1{
    width: 4rem;
    height: 0.3rem;
    padding: 0.05rem;
    margin:0.1rem 0;
    position: relative;
}
.num1 span:nth-of-type(1){
    float: left;
    position: absolute;
    top: 0.10rem;
    left:0;

}
.num1 .numipt{
    width: 0.6rem;
    position: absolute;
    top: 0.10rem;
    left:0.7rem;
}
.num1 span:nth-of-type(2){
    position: absolute;
    top: 0.10rem;
    left:0.5rem;
    font-size: 0.16rem;
    font-weight: bold;
}
.num1 span:nth-of-type(3){
    position: absolute;
    top: 0.10rem;
    left:1.5rem;
    font-size: 0.16rem;
    font-weight: bold;
}



.gooddetail{
    margin-left: .2rem
}
.gooddetail p:nth-of-type(1){
    font-size: .18rem;
    color: #000;
    font-weight: 600;
    width: 2.5rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.gooddetail div{
    color: red;
    margin-top: .15rem
}
.gooddetail div em{
    font-size: .20rem;
    font-style: normal
}
.gooddetail p:nth-of-type(2){
    color: red;
    border:  solid .01rem #f00;
    font-size: .12rem;
    border-radius: 0.12rem
}
.peisong{
    display: flex;
    justify-content: space-between;
    padding-top: .3rem
}
.peisong span:nth-child(1){
    color: #888
}
.num{
    position: absolute;
    right: .15rem;
    top: 0.8rem
}
.fapiao{
    width: 3.64rem;
    height: 0.5rem;
    box-shadow: 0 0 .04rem #eee;
    border-radius: .1rem;
    margin: .1rem ;
    padding: .2rem .15rem;
}
.fapiao div{
    display: flex;
}
.fapiao span:nth-child(1){
    width: 1rem;
    display: block
}
.fapiao span:nth-child(2){
    margin-left: 1.7rem;
    width: 1rem;
    text-align: right
}
.fapiao span:nth-child(3){
    font-size: .12rem;
    margin: 0 .05rem
}
.fapiao div:nth-child(2){
    margin-top: .1rem
}

.Settlement{
    width: 3.64rem;
    height: 0.8rem;
    box-shadow: 0 0 .04rem #eee;
    border-radius: .1rem;
    margin: .1rem ;
    padding: .2rem .15rem;
}

.Settlement>div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: .1rem
}
.Settlement div:nth-child(1){
    margin-top: 0
}
.submit{
    width: 4.14rem;
    height: 0.6rem;
    box-shadow: 0 0 .06rem #eee;
    position: fixed;
    left: 0;
    bottom: 0;
    line-height: .6rem;
    display: flex;justify-content: space-between
}

.submit div{
    color: red;
    height: 0.6rem;
    margin-left: .1rem
}
.submit div em{
    font-size: .20rem;
    font-style: normal
}
.submit p{
    width: 1rem;
    height: .6rem;
    background: #f00;
    text-align: center;
    line-height: .6rem;
    color: #fff;
    font-size: .16rem
}